/**
 * Team Modal Management
 * Handles showing team details and tab switching
 */

import { capitalize, escapeHtml } from '../utils/formatting.js';
import {
  getTeamName,
  getRank,
  calculateTeamStats,
} from '../utils/team-statistics.js';
import { fetchTeamMembers, getTeamUrl } from '../api/github-teams.js';
// Auth module used for token checks in settings
import { openSettings } from './settings.js';
import { getIcon } from '../config/icons.js';
import { loadChecks } from '../api/checks.js';
import { calculateCheckAdoptionByTeam } from '../utils/check-statistics.js';
import { createTabManager, type TabManager } from './modals/shared/tab-manager.js';
import type {
  ServiceData,
  RankName,
  RankCounts,
  TeamMember,
} from '../types/index.js';

// Window types are defined in types/globals.d.ts

interface TeamWithGitHub {
  name: string;
  serviceCount: number;
  averageScore: number;
  rankDistribution: RankCounts;
  staleCount: number;
  installedCount: number;
  github_org?: string | null;
  github_slug?: string | null;
  slack_channel?: string;
  oncall_rotation?: string;
  description?: string;
  aliases?: string[];
  id?: string;
}

interface CheckAdoptionTeamStats {
  passing: number;
  failing: number;
  excluded?: number;
  total: number;
  activeTotal?: number;
  percentage: number;
  services: Array<{
    org: string;
    repo: string;
    name: string;
    score: number;
    rank: RankName;
    checkStatus?: 'pass' | 'fail' | 'excluded' | 'error' | 'skipped' | 'unknown';
    exclusionReason?: string;
  }>;
}

// State for check adoption tab
let currentTeamServices: ServiceData[] = [];
let selectedCheckId: string | null = null;
let teamCheckDropdownOpen = false;
let teamTabManager: TabManager | null = null;

/**
 * Render GitHub team section HTML
 */
function renderGitHubSection(
  team: TeamWithGitHub,
  members: TeamMember[] | null
): string {
  const teamUrl = getTeamUrl(team.github_org!, team.github_slug!);

  // Not authenticated
  if (members === null) {
    return `
            <div class="team-github-section">
                <a href="${escapeHtml(teamUrl)}" target="_blank" class="team-github-link">
                    ${getIcon('github')} ${escapeHtml(team.github_slug!)}
                    ${getIcon('externalLink')}
                </a>
                <div class="team-github-signin">
                    <button class="btn-link" onclick="window.openSettingsForTeam()">
                        Sign in to view team members
                    </button>
                </div>
            </div>
        `;
  }

  // Authenticated but no members or error
  if (!members || members.length === 0) {
    return `
            <div class="team-github-section">
                <a href="${escapeHtml(teamUrl)}" target="_blank" class="team-github-link">
                    ${getIcon('github')} ${escapeHtml(team.github_slug!)}
                    ${getIcon('externalLink')}
                </a>
                <div class="team-members-empty">No members found or unable to access team</div>
            </div>
        `;
  }

  // Has members
  const membersHtml = members
    .map(
      (m) => `
        <a href="${escapeHtml(m.html_url)}" target="_blank" class="team-member" title="${escapeHtml(m.login)}">
            <img src="${escapeHtml(m.avatar_url)}&s=48" alt="${escapeHtml(m.login)}" class="team-member-avatar">
            <span class="team-member-name">@${escapeHtml(m.login)}</span>
        </a>
    `
    )
    .join('');

  return `
        <div class="team-github-section">
            <a href="${escapeHtml(teamUrl)}" target="_blank" class="team-github-link">
                ${getIcon('github')} ${escapeHtml(team.github_slug!)}
                ${getIcon('externalLink')}
            </a>
            <div class="team-members">
                <span class="members-label">Members (${members.length}):</span>
                <div class="members-grid">
                    ${membersHtml}
                </div>
            </div>
        </div>
    `;
}

/**
 * Show team detail modal
 */
export async function showTeamModal(teamName: string): Promise<void> {
  // Lazily compute team data if not already available
  if (!window.allTeams || window.allTeams.length === 0) {
    const services = window.allServices || [];
    if (services.length > 0) {
      const teamData = calculateTeamStats(services);
      window.allTeams = Object.values(teamData);
    }
  }

  const team = window.allTeams?.find(
    (t) => t.name === teamName
  ) as TeamWithGitHub | undefined;
  if (!team) {
    console.error('Team not found:', teamName);
    return;
  }

  // Get services for this team
  const teamServices = (window.allServices || []).filter(
    (s) => getTeamName(s) === team.name
  );

  const modal = document.getElementById('team-modal');
  const content = document.getElementById('team-detail');

  if (!modal || !content) {return;}

  const dominantRank = getRank(team);
  const rankDist: RankCounts = team.rankDistribution || {
    platinum: 0,
    gold: 0,
    silver: 0,
    bronze: 0,
  };

  // Build rank distribution bars
  const rankBars = (['platinum', 'gold', 'silver', 'bronze'] as RankName[])
    .map((rank) => {
      const count = rankDist[rank] || 0;
      const pct =
        team.serviceCount > 0
          ? Math.round((count / team.serviceCount) * 100)
          : 0;
      return `
            <div class="rank-dist-row">
                <span class="rank-dist-label">${capitalize(rank)}</span>
                <div class="rank-dist-bar-container">
                    <div class="rank-dist-bar rank-${rank}" style="width: ${pct}%"></div>
                </div>
                <span class="rank-dist-count">${count}</span>
            </div>
        `;
    })
    .join('');

  // Build services list
  const servicesList = teamServices
    .map((s) => {
      const score = s.score;
      const rank = s.rank;
      const scoreDisplay = score !== null && score !== undefined ? Math.round(score) : '-';
      const rankClass = rank ? `rank-${rank}` : '';
      return `
            <div class="team-service-item" onclick="window.showServiceDetail && window.showServiceDetail('${s.org}', '${s.repo}')">
                <span class="service-name">${escapeHtml(s.repo)}</span>
                <span class="service-score ${rankClass}">${scoreDisplay}</span>
            </div>
        `;
    })
    .join('');

  const teamId =
    team.id || team.name.toLowerCase().replace(/\s+/g, '-');

  content.innerHTML = `
        <div class="rank-badge modal-header-badge ${dominantRank}">${capitalize(dominantRank)}</div>
        <h2>${escapeHtml(team.name)} <button class="edit-icon-btn" onclick="window.openTeamEditModal && window.openTeamEditModal('${escapeHtml(teamId)}')" title="Edit Team"><svg width="14" height="14" viewBox="0 0 16 16" fill="currentColor"><path d="M11.013 1.427a1.75 1.75 0 012.474 0l1.086 1.086a1.75 1.75 0 010 2.474l-8.61 8.61c-.21.21-.47.364-.756.445l-3.251.93a.75.75 0 01-.927-.928l.929-3.25c.081-.286.235-.547.445-.758l8.61-8.61zm.176 4.823L9.75 4.81l-6.286 6.287a.253.253 0 00-.064.108l-.558 1.953 1.953-.558a.253.253 0 00.108-.064l6.286-6.286zm1.238-3.763a.25.25 0 00-.354 0L10.811 3.75l1.439 1.44 1.263-1.263a.25.25 0 000-.354l-1.086-1.086z"></path></svg></button></h2>
        <div class="team-metadata">
            <span class="team-id">ID: ${escapeHtml(teamId)}</span>
            ${team.description ? `<p class="team-description">${escapeHtml(team.description)}</p>` : ''}
            ${
  team.aliases && team.aliases.length > 0
    ? `
            <div class="team-aliases">
                <span class="aliases-label">Also known as:</span>
                ${team.aliases.map((a) => `<span class="alias-tag">${escapeHtml(a)}</span>`).join('')}
            </div>
            `
    : ''
}
        </div>

        <div class="team-modal-stats">
            <div class="team-modal-stat">
                <span class="stat-value">${Math.round(team.averageScore || 0)}</span>
                <span class="stat-label">Average Score</span>
            </div>
            <div class="team-modal-stat">
                <span class="stat-value">${team.serviceCount}</span>
                <span class="stat-label">Services</span>
            </div>
            <div class="team-modal-stat">
                <span class="stat-value">${team.installedCount}</span>
                <span class="stat-label">Installed</span>
            </div>
            <div class="team-modal-stat ${team.staleCount > 0 ? 'warning' : ''}">
                <span class="stat-value">${team.staleCount || 0}</span>
                <span class="stat-label">Stale</span>
            </div>
        </div>

        ${
  team.slack_channel || team.oncall_rotation
    ? `
        <div class="team-modal-contact">
            ${team.slack_channel ? `<span class="contact-item"><svg width="14" height="14" viewBox="0 0 16 16" fill="currentColor"><path d="M5.2 8.4a1.2 1.2 0 102.4 0 1.2 1.2 0 00-2.4 0zm6 0a1.2 1.2 0 102.4 0 1.2 1.2 0 00-2.4 0z"/></svg> ${escapeHtml(team.slack_channel)}</span>` : ''}
            ${team.oncall_rotation ? `<span class="contact-item"><svg width="14" height="14" viewBox="0 0 16 16" fill="currentColor"><path d="M8 0a8 8 0 1 1 0 16A8 8 0 0 1 8 0ZM1.5 8a6.5 6.5 0 1 0 13 0 6.5 6.5 0 0 0-13 0Zm7-3.25v2.992l2.028.812a.75.75 0 0 1-.557 1.392l-2.5-1A.75.75 0 0 1 7 8.25v-3.5a.75.75 0 0 1 1.5 0Z"/></svg> ${escapeHtml(team.oncall_rotation)}</span>` : ''}
        </div>
        `
    : ''
}

        <div class="tabs">
            <button class="tab-btn active" data-tab="services" onclick="switchTeamModalTab('services')">Services</button>
            <button class="tab-btn" data-tab="distribution" onclick="switchTeamModalTab('distribution')">Distribution</button>
            <button class="tab-btn" data-tab="adoption" onclick="switchTeamModalTab('adoption')">Check Adoption</button>
            <button class="tab-btn" data-tab="github" onclick="switchTeamModalTab('github')">GitHub</button>
        </div>

        <div class="team-tab-content tab-content active" id="team-tab-services">
            <div class="team-services-list">
                ${servicesList || '<div class="empty-state">No services in this team</div>'}
            </div>
        </div>

        <div class="team-tab-content tab-content" id="team-tab-distribution">
            <div class="rank-distribution-detail">
                ${rankBars}
            </div>
        </div>

        <div class="team-tab-content tab-content" id="team-tab-adoption">
            <div class="check-adoption-loading">
                <span class="loading-spinner"></span> Loading check data...
            </div>
        </div>

        <div class="team-tab-content tab-content" id="team-tab-github">
            ${
  team.github_org && team.github_slug
    ? `
                <div class="team-github-section team-github-loading">
                    <span class="loading-spinner"></span> Loading GitHub team...
                </div>
            `
    : `
                <div class="team-not-linked">
                    Not linked to a GitHub team
                </div>
            `
}
        </div>
    `;

  // Store team services for check adoption tab
  currentTeamServices = teamServices;

  // Initialize tab manager for team modal
  teamTabManager = createTabManager({
    containerId: 'team-modal',
    tabButtonSelector: '.tab-btn',
    tabContentSelector: '.team-tab-content',
    getTabContentId: (tabName: string) => `team-tab-${tabName}`,
    onActivate: {
      adoption: loadCheckAdoptionTab,
    },
  });

  modal.classList.remove('hidden');

  // Fetch GitHub team members asynchronously if team is linked
  if (team.github_org && team.github_slug) {
    const members = await fetchTeamMembers(team.github_org, team.github_slug);
    const section = document.getElementById('team-tab-github');
    if (section) {
      section.innerHTML = renderGitHubSection(team, members);
    }
  }
}

/**
 * Switch tabs within the team modal
 */
export function switchTeamModalTab(tabName: string): void {
  if (teamTabManager) {
    teamTabManager.switchTab(tabName);
  }
}

/**
 * Load and render check adoption tab content
 */
async function loadCheckAdoptionTab(): Promise<void> {
  const section = document.getElementById('team-tab-adoption');
  if (!section) {return;}

  try {
    // Load checks metadata
    const checksData = await loadChecks();
    const checks = checksData.checks || [];

    if (checks.length === 0) {
      section.innerHTML =
        '<div class="empty-state">No check metadata available</div>';
      return;
    }

    // Default to first check if none selected
    if (!selectedCheckId) {
      selectedCheckId = checks[0].id;
    }

    renderCheckAdoptionContent(section, checks);
  } catch (error) {
    console.error('Failed to load check adoption:', error);
    section.innerHTML =
      '<div class="empty-state">Failed to load check data</div>';
  }
}

/**
 * Render check adoption content
 */
function renderCheckAdoptionContent(
  section: HTMLElement,
  checks: Array<{ id: string; name: string; description?: string }>
): void {
  const services = currentTeamServices;
  const selectedCheck =
    checks.find((c) => c.id === selectedCheckId) || checks[0];

  // Calculate adoption for selected check
  const teamStats = calculateCheckAdoptionByTeam(services, selectedCheckId!);
  const currentTeamName = services[0]
    ? getTeamName(services[0]) || 'No Team'
    : 'No Team';
  const stats: CheckAdoptionTeamStats = teamStats[currentTeamName] || {
    passing: 0,
    failing: 0,
    excluded: 0,
    total: 0,
    activeTotal: 0,
    percentage: 0,
    services: [],
  };

  // Build check selector options
  const checkOptions = checks
    .map(
      (check) => `
        <div class="check-selector-option ${check.id === selectedCheckId ? 'selected' : ''}"
             data-check-id="${check.id}"
             onclick="window.selectTeamCheck('${check.id}')">
            ${escapeHtml(check.name)}
        </div>
    `
    )
    .join('');

  // Build passing/failing/excluded service lists
  const passingServices = stats.services.filter((s) => s.checkStatus === 'pass');
  const failingServices = stats.services.filter((s) => s.checkStatus === 'fail');
  const excludedServices = stats.services.filter(
    (s) => s.checkStatus === 'excluded'
  );

  const passingList =
    passingServices.length > 0
      ? passingServices
        .map(
          (s) => `
            <div class="adoption-service-item passing" onclick="window.showServiceDetail && window.showServiceDetail('${s.org}', '${s.repo}')">
                <span class="service-name">${escapeHtml(s.name)}</span>
                <span class="service-score rank-${s.rank}">${Math.round(s.score)}</span>
            </div>
        `
        )
        .join('')
      : '<div class="empty-list">No passing services</div>';

  const failingList =
    failingServices.length > 0
      ? failingServices
        .map(
          (s) => `
            <div class="adoption-service-item failing" onclick="window.showServiceDetail && window.showServiceDetail('${s.org}', '${s.repo}')">
                <span class="service-name">${escapeHtml(s.name)}</span>
                <span class="service-score rank-${s.rank}">${Math.round(s.score)}</span>
            </div>
        `
        )
        .join('')
      : '<div class="empty-list">No failing services</div>';

  const excludedList =
    excludedServices.length > 0
      ? excludedServices
        .map(
          (s) => `
            <div class="adoption-service-item excluded" onclick="window.showServiceDetail && window.showServiceDetail('${s.org}', '${s.repo}')">
                <span class="service-name">${escapeHtml(s.name)}</span>
                <span class="exclusion-reason" title="${escapeHtml(s.exclusionReason || 'Excluded')}">${escapeHtml(s.exclusionReason || 'Excluded')}</span>
            </div>
        `
        )
        .join('')
      : '<div class="empty-list">No excluded services</div>';

  // Calculate active services for progress display
  const activeTotal = stats.activeTotal || stats.total - (stats.excluded || 0);

  section.innerHTML = `
        <div class="check-adoption-content">
            <div class="check-selector">
                <label>Select Check:</label>
                <div class="check-selector-dropdown">
                    <button class="check-selector-toggle" onclick="window.toggleTeamCheckDropdown()">
                        <span class="check-selector-text">${escapeHtml(selectedCheck.name)}</span>
                        ${getIcon('chevronDown', { size: 16 })}
                    </button>
                    <div class="check-selector-menu" id="team-check-selector-menu">
                        <div class="check-selector-search">
                            <input type="text" placeholder="Search checks..."
                                   oninput="window.filterTeamCheckOptions(this.value)">
                        </div>
                        <div class="check-selector-options">
                            ${checkOptions}
                        </div>
                    </div>
                </div>
            </div>

            <div class="check-info">
                <p class="check-description">${escapeHtml(selectedCheck.description || '')}</p>
            </div>

            <div class="adoption-progress">
                <div class="progress-header">
                    <span class="progress-label">Adoption Rate</span>
                    <span class="progress-value">
                        ${stats.percentage}% (${stats.passing}/${activeTotal} active)
                        ${excludedServices.length > 0 ? `<span class="excluded-note">${excludedServices.length} excluded</span>` : ''}
                    </span>
                </div>
                <div class="progress-bar-large">
                    <div class="progress-fill ${stats.percentage >= 80 ? 'high' : stats.percentage >= 50 ? 'medium' : 'low'}" style="width: ${stats.percentage}%"></div>
                </div>
            </div>

            <div class="adoption-lists ${excludedServices.length > 0 ? 'three-columns' : ''}">
                <div class="adoption-column passing">
                    <h4>Passing (${passingServices.length})</h4>
                    <div class="adoption-service-list">
                        ${passingList}
                    </div>
                </div>
                <div class="adoption-column failing">
                    <h4>Failing (${failingServices.length})</h4>
                    <div class="adoption-service-list">
                        ${failingList}
                    </div>
                </div>
                ${
  excludedServices.length > 0
    ? `
                <div class="adoption-column excluded">
                    <h4>Excluded (${excludedServices.length})</h4>
                    <div class="adoption-service-list excluded-list">
                        ${excludedList}
                    </div>
                </div>
                `
    : ''
}
            </div>
        </div>
    `;
}

/**
 * Change selected check in team modal
 */
export function changeTeamCheck(checkId: string): void {
  selectedCheckId = checkId;
  loadCheckAdoptionTab();
}

/**
 * Toggle team check dropdown
 */
export function toggleTeamCheckDropdown(): void {
  const menu = document.getElementById('team-check-selector-menu');
  if (!menu) {return;}

  teamCheckDropdownOpen = !teamCheckDropdownOpen;
  menu.classList.toggle('open', teamCheckDropdownOpen);

  if (teamCheckDropdownOpen) {
    // Focus search input
    const searchInput = menu.querySelector('input') as HTMLInputElement | null;
    if (searchInput) {
      searchInput.value = '';
      searchInput.focus();
    }
    // Reset filter
    filterTeamCheckOptions('');

    // Add click-outside listener
    setTimeout(() => {
      document.addEventListener('click', handleClickOutsideTeamCheckDropdown);
    }, 0);
  } else {
    document.removeEventListener('click', handleClickOutsideTeamCheckDropdown);
  }
}

/**
 * Handle click outside team check dropdown
 */
function handleClickOutsideTeamCheckDropdown(e: Event): void {
  const dropdown = document
    .querySelector('#team-check-selector-menu')
    ?.closest('.check-selector-dropdown');
  if (dropdown && !dropdown.contains(e.target as Node)) {
    closeTeamCheckDropdown();
  }
}

/**
 * Close team check dropdown
 */
function closeTeamCheckDropdown(): void {
  const menu = document.getElementById('team-check-selector-menu');
  if (menu) {
    menu.classList.remove('open');
    teamCheckDropdownOpen = false;
  }
  document.removeEventListener('click', handleClickOutsideTeamCheckDropdown);
}

/**
 * Select check from team dropdown
 */
export function selectTeamCheck(checkId: string): void {
  selectedCheckId = checkId;
  closeTeamCheckDropdown();
  loadCheckAdoptionTab();
}

/**
 * Filter team check options by search query
 */
export function filterTeamCheckOptions(query: string): void {
  const options = document.querySelectorAll<HTMLElement>(
    '#team-check-selector-menu .check-selector-option'
  );
  const lowerQuery = query.toLowerCase();

  options.forEach((option) => {
    const text = option.textContent?.toLowerCase() || '';
    option.style.display = text.includes(lowerQuery) ? '' : 'none';
  });
}

// Expose dropdown functions to window
window.changeTeamCheck = changeTeamCheck;
window.toggleTeamCheckDropdown = toggleTeamCheckDropdown;
window.selectTeamCheck = selectTeamCheck;
window.filterTeamCheckOptions = filterTeamCheckOptions;

/**
 * Close team detail modal
 */
export function closeTeamModal(): void {
  const modal = document.getElementById('team-modal');
  if (modal) {
    modal.classList.add('hidden');
  }
}

// Expose to window for onclick handlers
window.showTeamDetail = showTeamModal;
window.closeTeamModal = closeTeamModal;
window.switchTeamModalTab = switchTeamModalTab;
window.openSettingsForTeam = openSettings;
